import { ChevronRightIcon, SearchIcon } from "@chakra-ui/icons";
import {
  Button,
  Center,
  Flex,
  Input,
  InputGroup,
  InputRightElement,
  Text,
} from "@chakra-ui/react";
import { useState, useEffect, useCallback, useMemo } from "react";
import { sendMessage } from "src/services/extension";
import useFetch from "use-http";

export default function Home() {
  const [url, setUrl] = useState<string>("");
  const [isShowDetail, setIsShowDetail] = useState<boolean>(false);
  const [prediction, setPrediction] = useState<{
    phishingPercentage: number;
    result: string;
    url: string;
  }>({
    phishingPercentage: 0,
    result: "",
    url: "",
  });
  const [detail, setDetail] = useState<any>();
  const [search, setSearch] = useState<string>("");

  const { post: getPredict } = useFetch("/predict");

  const { post: getDetail } = useFetch("/detail");

  const getURL = useCallback(async () => {
    const data = await sendMessage({ type: "get_page_info" });
    setUrl(data.url || "");
  }, []);

  const isNewTab = useMemo(() => url.includes("chrome://"), [url]);

  useEffect(() => {
    getURL();
    if (!!url) {
      getPredict({ url }).then((res) => {
        setPrediction(res.predictions[0]);
      });
    }
  }, [getURL, getPredict, url]);

  useEffect(() => {
    if (!!url) {
      getDetail({ url }).then((res) => {
        console.log(res);
        setDetail(res.detail);
      });
    }
  }, [getDetail, url]);

  if (isNewTab) return <Center>New tab</Center>;

  return (
    <Flex direction="column">
      <InputGroup mx={4} mt={4}>
        <Input
          defaultValue={url}
          value={search}
          onChange={(e) => {
            setSearch(e.target.value);
          }}
        ></Input>
        <InputRightElement children={<SearchIcon />} />
      </InputGroup>
      <Center>{url}</Center>
      <Center>{prediction.result}</Center>
      <Center>{100 - prediction.phishingPercentage}</Center>
      {detail && (
        <Flex justify="center" align="center" w="100%">
          <Button onClick={() => setIsShowDetail((prev) => !prev)}>
            Detail <ChevronRightIcon />
          </Button>
        </Flex>
      )}
      {isShowDetail && detail && (
        <Flex direction="column" justify="center" align="center" w="100%">
          {Object.keys(detail).map((item) => (
            <Text>
              {item}: {detail[item]}
            </Text>
          ))}
        </Flex>
      )}
    </Flex>
  );
}
